// html
<template>
  <div id='main'>
    <div id='center'>
      <div class='canteen-header'>
        <div id='searchBox' :class="{'fixed-searchBox': fixedSearch, 'searching': searchStatus}">
          <div v-show="searchStatus" class="back left" @click="endSearch"></div>
          <div class='search' :class="{'fixed-search': fixedSearch & !searchStatus, 'searching': searchStatus}" @click="startSearch">
            <input id="search-input" type='text' placeholder='搜索食堂、菜品名称' @keydown.enter="search">
          </div>
          <div v-show="searchStatus" class="search-btn right" @click="search"></div>
        </div>
      </div>
      <div v-show="searchStatus" class="search-panel">
        <div class="search-wrapper">
          <div v-for="(item, index) in searchList" v-bind:key="index" class='food-item'>
            <div class='item-content' @click='$root.learnFood(0, item)'>
              <div class='fd-pic' :style="$root.renderFoodPic(item.photo)"></div>
              <div class='fd-intro'>
                <p class="single-wrap">{{item.name}}</p>
                <p>月售357份</p>
                <p>好评率96%</p>
                <p>￥{{item.price}}</p>
              </div>
            </div>
            <div class='cartBox'>
              <img src='static/assets/buy.png' alt='buy'>
            </div>
          </div>
        </div>
      </div>
      <div class='canteen-content'>
        <div class='canteen-bar'>
          <table>
            <tr>
              <td v-for="(item, index) in restaurant.slice(0, 4)" v-bind:key="index">
                <div @click="enterRestaurant(index)"><p>{{item.name}}</p></div>
              </td>
            </tr>
            <tr>
              <td v-for="(item, index) in restaurant.slice(4, 8)" v-bind:key="index">
                <div @click="enterRestaurant(index + 4)"><p>{{item.name}}</p></div>
              </td>
            </tr>
          </table>
        </div>
        <div id='main-menu' class='menu'>
          <div id='recommend'>
            <img src='static/assets/recommend.png' alt='recommend'>
            <p>鸡胸肉扒饭</p>
            <div id='rec-intro'>
              <p>鲜嫩的鸡胸肉，配上特制的酱汁，口感饱满，色味俱佳，让人垂涎欲滴，食指大动。</p>
            </div>
            <img src='static/assets/food.jpeg' alt='food'>
          </div>
          <div>

          </div>
          <div>

          </div>
          <div>

          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import $ from 'jquery'
  export default {
    name: 'center',
    data () {
      return {
        fixedSearch: false,
        manual: false
      }
    },
    methods: {
      endSearch: function () {
        this.manual = false
        $('#search-input').val('')
        this.$store.commit('setSearchStatus', false)
        this.$store.commit('setSearchWords', '')
        this.$store.commit('setSearchList', [])
        this.$router.push({path: '/center'})
      },
      enterRestaurant: function (i) {
        this.$store.commit('enterRestaurant', this.$store.state.restaurantList[i].id)
        this.$router.push({path: '/restaurant/' + this.$store.state.restaurantList[i].id})
      },
      startSearch: function () {
        this.manual = true
        this.$router.push({path: '/center/search'})
        this.$store.commit('setSearchStatus', true)
        $('#search-input').focus()
      },
      search: function () {
        let self = this
        let words = $('#search-input').val()
        this.$store.commit('setSearchWords', words)
        $.ajax({
          async: false,
          url: '/search_dishes/',
          type: 'POST',
          data: {
            words: words,
            // eslint-disable-next-line no-undef
            csrfmiddlewaretoken: csrf_token
          },
          success: function (data) {
            self.$store.commit('setSearchList', data)
          },
          error: function () {
            alert('搜索失败！')
          }
        })
      }
    },
    computed: {
      scrollTop: function () {
        return this.$store.state.scrollTop
      },
      restaurant: function () {
        return this.$store.state.restaurantList
      },
      searchStatus: function () {
        return this.$store.state.searchStatus
      },
      searchWords: function () {
        return this.$store.state.searchWords
      },
      searchList: function () {
        return this.$store.state.searchList
      }
    },
    watch: {
      '$route': function (to, from) {
        if (to.name === 'center' && from.name === 'search') {
          this.manual = false
          $('#search-input').val('')
          this.$store.commit('setSearchStatus', false)
          this.$store.commit('setSearchWords', '')
          this.$store.commit('setSearchList', [])
        } else if (to.name === 'search' && from.name === 'center') {
          if (!this.manual) {
            this.$router.push({path: '/center'})
          }
        }
      },
      scrollTop: function (nval) {
        if (nval >= 240) {
          this.fixedSearch = this.fixedSearch ? this.fixedSearch : !this.fixedSearch
        } else if (this.fixedSearch) {
          this.fixedSearch = false
        }
      }
    },
    mounted () {
      $('#search-input').val(this.searchWords)
      $('document').scrollTop(0)
    }
  }
</script>

// css
<style scoped>
  .canteen-header{
    width: 100%;
    height: 420px;
    background: url('../../static/assets/header.jpg') 0 56%;
    background-size: cover;
  }
  #searchBox{
    width: 100%;
    height: 180px;
    position: fixed;
    top: 0;
    z-index: 2;
  }
  .fixed-searchBox{
    background-color: #1296db;
  }
  .back{
    width: 80px;
    height: 80px;
    margin: 52px 0 48px 32px;
    background: url('../../static/assets/back2.png') 50% 50%;
    background-size: cover;
  }
  .search{
    width: 90%;
    height: 100px;
    border-radius: 50px;
    background-color: white;
    position: fixed;
    top: 40px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 3;
    overflow: hidden;
    box-shadow: 0 3px 8px #707070;
    transition: width .5s;
  }
  .fixed-search{
    box-shadow: none;
  }
  .searching{
    width: 70%;
    background-color: white;
  }
  .search input{
    width: calc(100% - 100px);
    height: 100%;
    font-size: 36px;
    border: 0;
    outline: 0;
    position: relative;
    left: 50px;
  }
  .search-btn{
    width: 64px;
    height: 64px;
    margin: 54px 36px;
    background: url("../../static/assets/search2.png") 100% 50%;
    background-size: cover;
  }
  .search-panel{
    position: fixed;
    width: 100%;
    height: calc(100% - 150px);
    top: 150px;
    background-color: white;
    z-index: 10;
  }
  .search-wrapper{
    position: absolute;
    width: 100%;
    height: calc(100% - 40px);
    margin: 40px 0 0;
    overflow-x: hidden;
    overflow-y: auto;
  }
  .canteen-bar{
    width: 100%;
    height: 396px;
    margin: 12px 0;
  }
  .canteen-bar a{
    text-decoration: none;
    color: white;
  }
  .canteen-bar table{
    width: 100%;
    height: 100%;
  }
  .canteen-bar table div{
    width: 140px;
    height: 140px;
    border-radius: 70px;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    -webkit-transform: translateX(-50%);
  }
  .canteen-bar table div p{
    color: white;
    font-size: 36px;
    font-weight: 500;
    border: 0;
    width: 80%;
    text-align: center;
    position: relative;
    top: 50%;
    left: 10%;
    transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
  }
  .canteen-bar table tr:first-of-type td:first-of-type div{
    background-color: #FF4500;
  }
  .canteen-bar table tr:first-of-type td:nth-of-type(2) div{
    background-color: #FF8C00;
  }
  .canteen-bar table tr:first-of-type td:nth-of-type(3) div{
    background-color: #FFA500;
  }
  .canteen-bar table tr:first-of-type td:last-of-type div{
    background-color: #009B77;
  }
  .canteen-bar table tr:last-of-type td:first-of-type div{
    background-color: #1E90FF;
  }
  .canteen-bar table tr:last-of-type td:nth-of-type(2) div{
    background-color: #00BFFF;
  }
  .canteen-bar table tr:last-of-type td:nth-of-type(3) div{
    background-color: #9932CC;
  }
  .canteen-bar table tr:last-of-type td:last-of-type div{
    background-color: #C71585;
  }
  #main-menu{
    width: 90%;
    margin: 0 5%;
  }
  .menu{
    height: auto;
  }
  .menu>div{
    width: 100%;
    height: 340px;
    overflow: hidden;
  }
  .menu>div:first-of-type{
    border-top: 3px solid #f0f2f5;
  }
  .menu>div:not(:last-of-type){
    border-bottom: 3px solid #f0f2f5;
  }
  #recommend{
    height: 347px;
  }
  #recommend img:first-of-type{
    width: 52px;
    padding: 37px 25px 8px 0;
    float: left;
  }
  #recommend>p:first-of-type{
    font-size: 48px;
    font-weight: bold;
    line-height: 140%;
    padding-top: 30px;
    float: left;
  }
  #rec-intro{
    clear: left;
    width: calc(100% - 240px);
    height: calc(100% - 116px);
    display: table;
    float: left;
  }
  #rec-intro p{
    width: 100%;
    height: 100%;
    font-size: 36px;
    line-height: 200%;
    padding: auto;
    vertical-align: middle;
    display: table-cell;
  }
  #recommend img:last-of-type{
    width: 180px;
    height: 180px;
    border-radius: 12px;
    margin: 120px 0 45px 60px;
  }
</style>



